@mixin button-base {
  @apply inline-flex items-center justify-center;
  @apply box-border font-bold rounded-px10;
  @include focus-outline-base;
}

button {
  @include button-base;
}

.btn {
  @apply py-4 px-6;
  @include button-base;

  &-link {
	@apply inline-flex text-indigo font-bold border-b-2 pb-0.5;
  }

  &-secondary {
	@apply bg-indigo;
  }

  @screen md {
	@apply px-8;
  }

  @screen lg {
	@apply px-12;
  }
}
